@import 'ember-paper';
@import "ember-paper-expansion-panel";


.footer {
  padding: 0px 8px 8px 8px;
}

.credit {
  color: #999;
}

.landing-page-card {
  height: 100% !important;
}
// .landing-page-container {
//   height: 90vh;
// }
.landing-page-logo {
  padding: 24px 16px 16px;
}

.loading-text {
  margin-right: 10px;
}
.link-layer path {
  fill: none;
  stroke: #666;
  stroke-width: 1.5px;
}

.link-layer line.dotted {
  stroke-dasharray: 5;
}

.node-layer circle {
  pointer-events: all;
  stroke: none;
  stroke-width: 40px;
}

.cluster-layer {

}

circle {
  fill: #ccc;
  stroke: #333;
  stroke-width: 1.5px;
}

circle.enrichment {
  fill: red;
  stroke: black;
  stroke-width: 3px;
}

circle.selected {
  fill: blue;
  stroke: black;
  stroke-width: 3px;
}

text {
  font: 16px sans-serif;
  pointer-events: none;
}

text.shadow {
  stroke: #fff;
  stroke-width: 5px;
  stroke-opacity: .8;
}
.termlist {
  max-height: 90vh !important;
}

.scrollabletermlist {
  max-height: 100%;
  overflow-y: auto;
}

.term-ontology-card {
  max-height: 90vh;
}
.term-ontology-graph-container {
  height: 100%;
}

term-ontology-graph {
  max-height: 100%;
}

.zoom-layer {
  cursor: move;
  fill: none;
  pointer-events: all;
}

md-icon.term-selected {
  background-color: rgba(4, 0, 255, 0.38) !important;
}

.graph-options-slider {
  margin: 0px 15px 0px 15px;
}

.cluster-input-field {
  margin-top: 0px;
  margin-bottom: 0px;
}

.cluster-update-button {
  top: -14px;
}

.reclustering-text {
  z-index: 100;
  background: white;
  width: 100000%
}

.frost-text::after, .frost-text::before {
  box-sizing: unset;
}

.cluster-menu-offset-text {
  margin-left: 72px;
  margin-top: 0px;
  margin-bottom: 8px;
}

.cluster-menu-offset-text-header {
  margin-left: 72px;
  margin-top: 0px;
  margin-bottom: 10px;
}

.cluster-menu-link-text {
  color: RGB(var(--accent-500));
}

.cluster-menu-term-item {
  margin-top: -10px;
}

.reset-graph-button {
  width: 100%;
  margin-left: 0;
}
